@import 'global.scss';

:host {
  /**
  * @prop --navigation-element-text-color: The text color of the navigation elements.
  * @prop --navigation-element-text-hover-color: The text color of the navigation elements on hover.
  * @prop --navigation-element-text-active-color: The text color of the navigation elements on active state.
  * @prop --navigation-element-text-decoration: The text decoration of the navigation elements.
  * @prop --navigation-element-hover-text-decoration: The text decoration of the navigation elements on hover.
  * @prop --navigation-element-active-text-decoration: The text decoration of the navigation elements on active state.
  * @prop --navigation-element-background-color: The background color of the navigation link elements.
  * @prop --navigation-element-hover-background-color: The background color of the navigation link elements on hover state.
  * @prop --navigation-element-active-background-color: The background color of the navigation link elements on active state.
  *
  * @prop --navigation-element-font-family: The font family of the navigation elements.
  * @prop --navigation-element-font-size: The font size of the navigation elements.
  * @prop --navigation-element-font-weight: The font weight of the navigation elements.
  * @prop --navigation-element-line-height: The line height of the navigation elements.
  * @prop --navigation-element-link-padding: The padding around the link element.
  * @prop --navigation-element-link-display: The display mode of the link element.
  *
  * @prop --navigation-element-divider-width: The width of the divider.
  * @prop --navigation-element-divider-style: The style of the divider (border).
  * @prop --navigation-element-divider-border-color: The color of the divider.
  *
  * @prop --navigation-element-sub-navigation-dropdown-container-padding: The padding of the dropdown container containing the sub navigation.
  * @prop --navigation-element-sub-navigation-dropdown-border: The color of the border of the dropdown containing the sub navigation.
  * @prop --navigation-element-sub-navigation-dropdown-border-radius: The border radius of the dropdown containing the sub navigation.
  *
  * @prop --navigation-element-sub-navigation-dropdown-item-padding: The padding of the item inside the dropdown containing the sub navigation.
  * @prop --navigation-element-sub-navigation-dropdown-item-background-color: The background color of the item inside the dropdown containing the sub navigation.
  * @prop --navigation-element-sub-navigation-dropdown-item-hover-background-color: The background color of the item inside the dropdown containing the sub navigation on hover state.
  * @prop --navigation-element-sub-navigation-dropdown-min-width: The minimum width of the dropdown containing the sub navigation.
  *
  * @prop --navigation-element-sub-navigation-container-padding: The padding of the container of the accordion sub navigation.
  *
  * @prop --navigation-element-sub-navigation-accordion-heading-font-family: The default font family of the heading of the accordion containing sub navigation.
  * @prop --navigation-element-sub-navigation-accordion-heading-font-size: The font size of the heading of the accordion containing sub navigation.
  * @prop --navigation-element-sub-navigation-accordion-heading-font-weight: The font weight of the heading of the accordion containing sub navigation.
  * @prop --navigation-element-sub-navigation-accordion-heading-line-height: The line height of the heading of the accordion containing sub navigation.
  *
  * @prop --navigation-element-sub-navigation-accordion-heading-padding: The padding of the heading of the accordion containing sub navigation.
  * @prop --navigation-element-sub-navigation-accordion-content-padding: The padding of the content of the accordion containing sub navigation.
  * @prop --navigation-element-sub-navigation-accordion-divider-background-color: The background color of the divider of the accordion items.
  * @prop --navigation-element-sub-navigation-accordion-divider-height: The height of the divider of the accordion items.
  *
  * @prop --navigation-element-right-icon-open-transform: The transform of the right icon on sub navigation open state.
  * @prop --navigation-element-right-icon-transition: The transition of the right icon on sub navigation state change.
  *
  * @prop --navigation-element-animation-duration: The animation duration of the sub navigation state change.
  *
  * @prop display: The display mode of the host element.
  * @prop flex-direction: The flex direction of the host element.
  * @prop justify-content: The alignment of the content inside the host element.
  */
  display: block;
  
  --navigation-element-text-color: #{$rwc-color-text-primary};
  --navigation-element-text-hover-color: #{$rwc-color-text-primary};
  --navigation-element-text-active-color: #{$rwc-color-text-primary};
  --navigation-element-text-decoration: none;
  --navigation-element-hover-text-decoration: none;
  --navigation-element-active-text-decoration: none;
  --navigation-element-background-color: unset;
  --navigation-element-hover-background-color: unset;
  --navigation-element-active-background-color: unset;
  
  --navigation-element-font-family: #{$rwc-font-family-default};
  --navigation-element-font-size: #{$rwc-font-size-body-md};
  --navigation-element-font-weight: 500;
  --navigation-element-line-height: #{$rwc-line-height-xs};
  
  --navigation-element-link-padding: #{$rwc-spacing--2xs} #{$rwc-spacing--xs};
  --navigation-element-link-display: flex;
  
  --navigation-element-divider-width: 1px;
  --navigation-element-divider-style: solid;
  --navigation-element-divider-border-color: #{$rwc-color-border-primary};
  
  --navigation-element-sub-navigation-dropdown-container-padding: #{$rwc-spacing--xs} 0;
  --navigation-element-sub-navigation-dropdown-border: 1px solid #{$rwc-color-border-primary};
  --navigation-element-sub-navigation-dropdown-border-radius: #{$rwc-border-radius--md};
  
  --navigation-element-sub-navigation-dropdown-item-padding: #{$rwc-spacing--3xs} #{$rwc-spacing--xs};
  --navigation-element-sub-navigation-dropdown-item-background-color: transparent;
  --navigation-element-sub-navigation-dropdown-item-hover-background-color: #{$rwc-color-grey-200};
  --navigation-element-sub-navigation-dropdown-min-width: 6.25rem;
  
  --navigation-element-sub-navigation-container-padding: 0;
  
  --navigation-element-sub-navigation-accordion-heading-font-family: #{$rwc-font-family-default};
  --navigation-element-sub-navigation-accordion-heading-font-size: #{$rwc-font-size-heading-xs};
  --navigation-element-sub-navigation-accordion-heading-font-weight: 400;
  --navigation-element-sub-navigation-accordion-heading-line-height: #{$rwc-line-height-sm};
  
  --navigation-element-sub-navigation-accordion-heading-padding: #{$rwc-spacing--xs} 0;
  --navigation-element-sub-navigation-accordion-content-padding: 0 0 0 #{$rwc-spacing--2xs};
  --navigation-element-sub-navigation-accordion-divider-background-color: #{$rwc-color-divider-neutral};
  --navigation-element-sub-navigation-accordion-divider-height: 1px;
  
  --navigation-element-right-icon-open-transform: rotate(-180deg);
  --navigation-element-right-icon-transition: transform var(--navigation-element-animation-duration) ease-in-out;
  
  --navigation-element-animation-duration: 200ms;
  
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.container {
  font-family: var(--navigation-element-font-family);
  font-size: var(--navigation-element-font-size);
  font-weight: var(--navigation-element-font-weight);
  line-height: var(--navigation-element-line-height);
  padding: var(--navigation-element-container-padding);
  
  &:not(.divider-none) {
    border-color: var(--navigation-element-divider-border-color);
  }
  
  &.divider-bottom {
    border-bottom-width: var(--navigation-element-divider-width);
    border-bottom-style: var(--navigation-element-divider-style);
  }
  
  &.divider-right {
    border-right-width: var(--navigation-element-divider-width);
    border-right-style: var(--navigation-element-divider-style);
  }
}

.link {
  background-color: var(--navigation-element-background-color);
  color: var(--navigation-element-text-color);
  display: var(--navigation-element-link-display);
  flex-direction: row;
  justify-content: space-between;
  padding: var(--navigation-element-link-padding);
  transition:
    background-color var(--navigation-element-link-style-transition-duration) linear,
    color var(--navigation-element-link-style-transition-duration) linear;
  
  &:hover {
    background-color: var(--navigation-element-hover-background-color);
    color: var(--navigation-element-text-hover-color);
    
    .text {
      text-decoration: var(--navigation-element-hover-text-decoration);
    }
  }
  
  &:active {
    background-color: var(--navigation-element-active-background-color);
    color: var(--navigation-element-text-active-color);
    
    .text {
      text-decoration: var(--navigation-element-active-text-decoration);
    }
  }
}

.icon {
  display: flex;
  gap: var(--navigation-element-link-gap);
  flex-direction: column;
  justify-content: center;
  fill: #{$rwc-color-grey-400};
}

.icon-left {
  margin-right: var(--navigation-element-link-gap);
}

.icon-right {
  margin-left: var(--navigation-element-link-gap);
  transition: var(--navigation-element-right-icon-transition);
}

.text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-decoration: var(--navigation-element-text-decoration);
}

a {
  cursor: pointer;
  text-decoration: inherit;
  color: inherit;
}

.sub-navigation-dropdown {
  min-width: var(--navigation-element-sub-navigation-dropdown-min-width);
  
  --dropdown-container-padding: var(--navigation-element-sub-navigation-dropdown-container-padding);
  --dropdown-border: var(--navigation-element-sub-navigation-dropdown-border);
  --dropdown-border-radius: var(--navigation-element-sub-navigation-dropdown-border-radius);
  --dropdown-animation-duration: var(--navigation-element-animation-duration);
  
  --dropdown-item-padding: var(--navigation-element-sub-navigation-dropdown-item-padding);
  --dropdown-item-background-color: transparent;
  --dropdown-item-hover-background-color: #{$rwc-color-grey-200};
}

.sub-navigation-accordion {
  --accordion-default-font-family: var(--navigation-element-sub-navigation-accordion-heading-font-family);
  --accordion-heading-font-size: var(--navigation-element-sub-navigation-accordion-heading-font-size);
  --accordion-heading-font-weight: var(--navigation-element-sub-navigation-accordion-heading-font-weight);
  --accordion-content-line-height: var(--navigation-element-sub-navigation-accordion-heading-line-height);
  
  --accordion-heading-padding: var(--navigation-element-sub-navigation-accordion-heading-padding);
  --accordion-content-padding: var(--navigation-element-sub-navigation-accordion-content-padding);
  
  --accordion-animation-duration: var(--navigation-element-animation-duration);
  
  --accordion-divider-background-color: var(--navigation-element-sub-navigation-accordion-divider-background-color);
  --accordion-divider-height: var(--navigation-element-sub-navigation-accordion-divider-height);
  
  &.has-sub-navigation {
    --accordion-heading-font-weight: var(--navigation-element-font-weight);
  }
}

.sub-navigation {
  --navigation-container-padding: var(--navigation-element-sub-navigation-container-padding);
}

.dropdown-open {
  .icon {
    &.rotate {
      transform: var(--navigation-element-right-icon-open-transform); 
    }
  }
}
