import { Component, Element, Event, EventEmitter, Fragment, FunctionalComponent, h, Host, Listen, Prop, State } from '@stencil/core';
import { NavigationOpenMode, NavigationType, SubNavigationPosition } from '../../../interfaces/components/basic/navigation/navigation';
import { LinkElementRenderParams } from '../../../interfaces/components/basic/navigation/navigation-element';
import ChevronRight from '@material-design-icons/svg/round/chevron_right.svg';
import ExpandMore from '@material-design-icons/svg/round/expand_more.svg';

/**
 * @slot - Default slot for the navigation element text.
 * @slot before - Slot for content to be inserted before the navigation element text.
 * @slot after - Slot for content to be inserted after the navigation element text.
 * @slot sub-navigation - Slot for sub-navigation elements.
 */
@Component({
  tag: 'rwc-navigation-element',
  styleUrl: './navigation-element.scss',
  shadow: true
})
export class RwcNavigationElement {
  /**
   * The default href value of the navigation elements.
   */
  static NO_NAVIGATION_HREF = 'javascript:void(0)';

  /**
   * Reference to the host element of the component
   */
  @Element() host: HTMLRwcNavigationElementElement;

  /**
   * The link to navigate to on click
   */
  @Prop({ reflect: true }) link: string = RwcNavigationElement.NO_NAVIGATION_HREF;

  /**
   * Determines if the sub navigation opens on hover or on toggle click
   */
  @Prop({ reflect: true, mutable: true }) openMode: NavigationOpenMode = 'hover';

  /**
   * The type of the sub navigation
   */
  @Prop({ reflect: true }) subNavigationType: NavigationType = 'vertical';

  /**
   * The position of the sub navigation in relation to the navigation element
   */
  @Prop({ reflect: true }) subNavigationPosition: SubNavigationPosition = 'bottom';

  /**
   * The render mode of the dividers.
   */
  @Prop() renderDividers: boolean = false;

  /**
   * Enables rendering of the default sub navigation indicator icon.
   */
  @Prop() renderIcon: boolean = true;

  /**
   * Prevents default anchor element navigation behaviour.
   */
  @Prop() preventNavigation: boolean = false;

  /**
   * The path to the left icon image.
   */
  @Prop() iconLeft: string = '';

  /**
   * The path to the right icon image.
   */
  @Prop() iconRight: string = '';

  /**
   * The state of the dropdown.
   */
  @State() dropdownOpen: boolean = false;

  /**
   * The navigation event emitter.
   */
  @Event() navigate: EventEmitter<string>;

  /**
   * Determines if the navigation element contains sub navigation.
   */
  private get hasSubNavigation(): boolean {
    return Boolean(this.host.querySelector('[slot="sub-navigation"]'));
  }

  /**
   * Updates the dropdown open state of the navigation element based on the state of the dropdown.
   */
  @Listen('dropdownOpen')
  onDropdownOpen(event: CustomEvent<void>): void {
    if (event.target === this.host) {
      this.dropdownOpen = true;
    }
  }

  /**
   * Updates the dropdown open state of the navigation element based on the state of the dropdown.
   */
  @Listen('dropdownClose')
  onDropdownClose(event: CustomEvent<void>): void {
    if (event.target === this.host) {
      this.dropdownOpen = false;
    }
  }

  /**
   * StencilJS lifecycle event handler called when the component is to be first loaded.
   */
  componentWillLoad(): void {
    if (this.subNavigationType === 'accordion') {
      this.openMode = 'manual';
    }
  }

  /**
   * The navigation event handler.
   */
  private handleNavigation(event: MouseEvent): void {
    if (this.preventNavigation) {
      event.preventDefault();
    }

    const closestAnchor = (event.composedPath()[0] as HTMLElement).closest('a');
    if (closestAnchor) {
      this.navigate.emit(closestAnchor.href);
    }
  }

  /**
   * Gets the navigation element href for the link element based on sub navigation opening behaviour.
   */
  private getNavigationElementHref(): string {
    if (
      this.openMode === 'hover' &&
      this.subNavigationType !== 'accordion' ||
      !this.hasSubNavigation
    ) {
      return this.link
    }

    return RwcNavigationElement.NO_NAVIGATION_HREF;
  }

  /**
   * Renders the component.
   */
  render(): FunctionalComponent {
    return (
      <Host>
        <div class={ {
          container: true,
          [this.openMode]: true,
          divider: this.renderDividers,
          [`sub-nav-type-${ this.subNavigationType }`]: true,
          'dropdown-open': this.dropdownOpen
        } }>
          { this.renderNavigationElement() }
        </div>
      </Host>
    );
  }

  /**
   * Renders the navigation element based on the type of navigation.
   */
  private renderNavigationElement(): FunctionalComponent {
    if (this.subNavigationType === 'accordion') {
      return (
        <rwc-accordion
          class={ {
            'sub-navigation-accordion': true,
            'has-sub-navigation': this.hasSubNavigation
          } }
          renderDivider={ this.renderDividers }
          open={ false }
          renderIcon={ this.renderIcon && this.hasSubNavigation }
        >
          { this.renderLinkElement({ slot: 'heading' }) }
        </rwc-accordion>
      );
    }

    return this.renderLinkElement({ slot: null });
  }

  /**
   * Renders the link element.
   */
  private renderLinkElement(params: LinkElementRenderParams): FunctionalComponent {
    return (
      <Fragment>
        <a
          { ...{
            href: this.getNavigationElementHref(),
            'slot': params.slot || ''
          } }
          onClick={ (event: MouseEvent) => this.handleNavigation(event) }
        >
          <div class="link">
            <slot name="before">
              { this.renderLeftIcon() }
            </slot>
            <div class="text">
              <slot />
            </div>
            <slot name="after">
              { this.renderRightIcon() }
            </slot>
          </div>
        </a >
        { this.renderSubNavigationContainer() }
      </Fragment>
    )
  }

  /**
   * Renders sub navigation from the data prop.
   */
  private renderSubNavigationContainer(): FunctionalComponent {
    if (this.hasSubNavigation) {
      if (this.subNavigationType === 'accordion') {
        return (
          <rwc-navigation
            class="sub-navigation"
            type={ this.subNavigationType }
          >
            <slot name="sub-navigation" />
          </rwc-navigation>
        )
      } else {
        return (
          <rwc-dropdown
            class="sub-navigation-dropdown"
            openMode={ this.openMode }
            mode={ 'fixed' }
            position={ this.subNavigationPosition }
            size="auto"
          >
            <slot name="sub-navigation" />
          </rwc-dropdown>
        );
      }
    }
    return <Fragment></Fragment>;
  }

  /**
   * Render the left icon for the navigation element
   */
  private renderLeftIcon(): FunctionalComponent {
    if (this.iconLeft) {
      return (
        <img src={ this.iconLeft } alt="Left icon" class="icon icon icon-left"></img>
      );
    }
    return <Fragment></Fragment>;
  }

  /**
   * Renders the avatar element.
   */
  renderRightIcon(): FunctionalComponent {
    if (this.iconRight) {
      return (
        <img
          src={ this.iconRight }
          alt="Right icon"
          class={ {
            icon: true,
            ['icon-right']: true,
            rotate: this.subNavigationPosition === 'bottom' && this.subNavigationType !== 'accordion'
          } }
        />
      );
    } else if (this.subNavigationType === 'vertical' && this.renderIcon && this.hasSubNavigation) {
      return (
        <div
          innerHTML={ this.subNavigationPosition === 'side' ? ChevronRight : ExpandMore }
          class={ {
            icon: true,
            ['icon-right']: true,
            rotate: this.subNavigationPosition === 'bottom'
          } }
        >
        </div>
      );
    }
  }
}
